    monospace;
}

*{box-sizing:border-box}body{padding:0px;font-family:'Roboto', sans-serif;background-color:#202020}h1,h2,h3,h4,h5,h6{margin:0px}.tech-container{display:flex;text-align:center;color:white}.tab{padding:10px 0px;transition:0.3s}.tab .title{transition:0.3s}.tab .content{transition:0.5s}.tab-closed{width:80px;cursor:pointer}.tab-closed img{width:0}.tab-open{flex-grow:1;overflow:hidden;display:flex;flex-direction:column}.tab-open .item-img{margin:0.5rem auto;max-width:5rem;width:25%}.tab-open .item-img img{width:100%}.tab-open .item-text{color:white;font-size:120%}.tab-open .item-text:hover{color:whitesmoke}.title{color:white;text-shadow:0px 0px 5rem black}.title-closed{transform:matrix(0, -1, 1, 0, 0, 150)}.title-open{font-size:1.2rem}ul{padding:0;margin:0;list-style:none}.content-closed{opacity:0;transform:translateY(300px)}.content-open{opacity:1;flex-grow:1}.content-open ul{cursor:default;height:100%;display:flex;flex-direction:column;justify-content:space-around}@media screen and (max-width: 400px){.title-open h2{font-size:1.5rem}.tab-closed{width:60px}}.App{position:relative}.App section{height:calc(100vh - 6rem)}.main{height:100%}.app-header{height:3rem;padding:0 0.5rem;display:flex;background-color:#202020;justify-content:center}.logo{flex-grow:1}.nav-app-bar{box-shadow:none !important}.nav-app-bar .MuiTabs-indicator{background-color:red}.nav-app-bar div{background-color:#202020}.footer{height:3rem;width:100%;display:flex;padding:0 1rem;justify-content:space-between}.copyrights{color:white}.contact-btns span{color:white}.contact-btns span:hover{color:#1877f2}.contact-btns-container{transition:0.3s}.contact-hidden{opacity:0%;visibility:hidden}.hero-section{background-color:#000000;background-image:linear-gradient(315deg, #000 0%, #414141 74%)}.hero-container{width:100%;height:100%;display:flex;position:relative}.hero-container .hero-placeholder{flex-grow:1;background-color:#202020}.hero-container .img-container{flex-grow:1;background-color:#404040;background-image: